    opacity: 100%;
  }
}

@keyframes scaleUp{
  from{
    transform: scale(.7,.7);
    opacity: 0%;
  }

  to{
    transform: scale(1,1);
    opacity: 100%;
  }
}

@keyframes modalDown{
  0%{
    transform: translate(-50%, -40%);
    opacity: 0%;
  }

  100%{
    transform: translate(-50%, -50%);
    opacity: 100%;
  }
}

.animate-itemUp{
  animation: up 0.5s ease-in-out;
}

.animate-scale{
  animation: scaleUp 0.5s ease-in-out;
}

.animate-modalDown{
  animation: modalDown .3s ease-out;
}

